@charset 'utf-8';
@import '../../scss/utils';

.flicking {
  width: 100%;
  padding-top: 10px;
}

.button {
  overflow: hidden;
  position: relative;
  width: calc(100vw - 40px);
  max-width: 335px;
  height: 400px;
  border-radius: 10px;

  .item:first-child & {
    margin-left: 20px;
  }

  .item:last-child & {
    margin-right: 20px;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 120px;
    background-image: linear-gradient(to top, transparent, rgba(#000, 0.5));
  }
}

.image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.title {
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  font-size: 25px;
  color: #fff;
  z-index: 1;
}
