@charset 'utf-8';
@import '../scss/utils';

.layout {
  position: relative;
  min-width: $minWidth;
  max-width: 768px;
  height: 100%;
  margin: 0 auto;
  padding-bottom: 20px;
}

.content {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: $maxWidth;
}
